* {
  padding: 0;
  margin: 0;
}

.card {
  width: 100%;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 3px -1px rgba(151, 171, 187, 0.7);
  float: left;
  transform-style: preserve-3d;
  margin: 10px 5px;
}
.background_cover div:nth-child(0) {
  display: none;
}
.VsImage img {
  max-height: 200px;
}
.background_cover div {
  display: block;
  width: 100%;
  text-align: center;
}

.teamName p {
  text-transform: uppercase;
  font-weight: 700;
  margin: 10px 0 10px 0;
  border-bottom: 1px solid #ebeff2;
  padding: 19px 0;
  text-align: center;
  color: #59687f;
  font-size: 19px;
  position: relative;
}
.MuiCardContent-root {
  padding: 16px;
  padding-bottom: 0;
}
/*Heading Style Four*/
.sandbox {
  margin-bottom: 15px;
}
.sandbox-correct-pronounciation {
  padding: 1em 0 1em 0;
  background-color: rgb(33 56 111);
}

.heading-correct-pronounciation {
  margin: auto;
  text-align: center;
  position: relative;
}

h5 {
  font-family: "Cardo", serif;
  font-size: 1.5em;
  font-weight: normal;
  font-style: italic;
  letter-spacing: 0.1em;
  line-height: 2.2em;
  color: #fff;
}

em {
  font-family: "EB Garamond", serif;
  font-size: 3.5em;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  display: block;
  font-style: normal;
  padding-top: 0.1em;
  text-shadow: 0.07em 0.07em 0 rgba(0, 0, 0, 0.1);
}

.bottom {
  font-size: 1.5em;
  letter-spacing: 0.07em;
  font-size: 1em;
  display: block;
}
.match-date {
  font-size: 12px;
  line-height: 34px;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: absolute;
  top: 0px;
  right: 0;
  font-weight: 700;
  color: #fff;
  background: linear-gradient(to right, #58ade3, #327dc0);

  padding: 0 14px;
  height: 34px;
}
.match-status {
  font-size: 12px;
  line-height: 34px;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  font-weight: 700;
  color: #fff;
  padding: 0 14px;
  background: #39274f;
}
.show-button {
  font-weight: 700;
  padding: 8px 20px 8px 21px;
  text-align: center;
  text-transform: uppercase;
  background: linear-gradient(to right, #f7b733, #b58903);
  color: #fff;
  border: 3px solid #eee;
  margin-bottom: 25px;
  outline-style: none !important;
}

.popup {
  width: 600px;
  height: auto;
  padding: 25px;
  padding-bottom: 0;
}
.content {
  text-align: center;
}
.popup p {
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  margin: 10px 0 10px 0;
  border-bottom: 1px solid #ebeff2;
  padding: 0 0 20px 1px;
  text-align: center;
  color: #59687f;
  font-size: 19px;
  position: relative;
}
.btn-close {
  font-weight: 700;
  color: #fff !important;
  background: linear-gradient(to right, #58ade3, #327dc0);
  text-align: center;
  padding: 5px 39px 5px 39px;
  margin-bottom: 15px;
}
.description {
  border: 1px solid #ebeff2;
  border-radius: 5px;
  padding: 20px 0px;
  margin: 10px 20px;
  font-size: 16px;
  color: rgb(27 63 93);
  font-weight: 600;
  overflow: auto;
}
.section-padding {
  padding: 60px 0;
}
.bottom {
  background-color: rgb(0 7 14);
}
.bottom .copyright {
  color: #e5e5e5;
  font-weight: 600;
}
.copyright a {
  color: #f2ff49;
  margin-left: 3px;
  padding-right: 3px;
}
.bottom p {
  margin-bottom: 0;
  line-height: 50px;
  font-size: 16px;
  font-weight: 400;
}
.copyright p span {
  color: #d1caca;
}
.bottom .copyright p,
.bottom .copyright a:hover {
  color: #6c6d83;
}
button:disabled {
  cursor: not-allowed;
}
.custom-height {
  min-height: 100px;
}

.checkbox.checbox-switch {
  padding-left: 0;
  position: absolute;
  top: 15px;
  right: 0;
  z-index: 2;
}
.checkbox.checbox-switch {
  padding-left: 0;
}

.checkbox.checbox-switch label,
.checkbox-inline.checbox-switch {
  display: inline-block;
  position: relative;
  padding-left: 0;
  cursor: pointer;
}
.checkbox.checbox-switch label input,
.checkbox-inline.checbox-switch input {
  display: none;
}
.checkbox.checbox-switch label span,
.checkbox-inline.checbox-switch span {
  width: 58px;
  border-radius: 20px;
  height: 26px;
  border: 1px solid #dbdbdb;
  background-color: rgb(236 241 245);
  border-color: rgb(236 241 245);
  box-shadow: rgb(223, 223, 223) 0px 0px 0px 0px inset;
  transition: border 0.4s ease 0s, box-shadow 0.4s ease 0s;
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
}
.checkbox.checbox-switch label span:before,
.checkbox-inline.checbox-switch span:before {
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: rgb(255, 255, 255);
  content: " ";
  top: 0;
  position: relative;
  left: 0px;
  transition: all 0.3s ease;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
  top: -3px;
}
.checkbox.checbox-switch label > input:checked + span:before,
.checkbox-inline.checbox-switch > input:checked + span:before {
  left: 25px;
  top: -3px;
}

/* Switch Primary */
.checkbox.checbox-switch.switch-primary label > input:checked + span,
.checkbox-inline.checbox-switch.switch-primary > input:checked + span {
  background-color: #6c757d;
  border-color: #6c757d;
  /*box-shadow: rgb(0, 105, 217) 0px 0px 0px 8px inset;*/
  transition: border 0.4s ease 0s, box-shadow 0.4s ease 0s,
    background-color 1.2s ease 0s;
}
.checkbox.checbox-switch.switch-primary label > input:checked:disabled + span,
.checkbox-inline.checbox-switch.switch-primary > input:checked:disabled + span {
  background-color: rgb(236 241 245);
  border-color: rgb(236 241 245);
  /* box-shadow: rgb(109, 163, 221) 0px 0px 0px 8px inset;*/
  transition: border 0.4s ease 0s, box-shadow 0.4s ease 0s,
    background-color 1.2s ease 0s;
}

@media (max-width: 700px) {
  .checkbox.checbox-switch {
    padding-left: 0;
    position: absolute;

    right: 0;
    top: 8%;
  }
  em {
    font-size: 1.5em;

    line-height: 15px;
  }
  .popup {
    width: 100%;
  }
}
